e; por ejemplo, en la calle, es fácil encontrar vallas y avisos en los almacenes, las tiendas, las vías, etcétera, en los que se cometen fallas, algunas veces producen risa y en otras, vergüenza. Muchos de los errores tienen que ver con el desconocimiento de las normas. La falta de lectura de quienes producen la información, la influencia de las redes sociales y las interacciones diarias en chats, han producido un descuido gramatical, sumado a la falta de corrección y crítica, lo cual ha conllevado escribir con errores.")]),_c('div',{staticClass:"row mb-4"},[_c('div',{staticClass:"col-auto d-none d-lg-flex"},[_c('figure',[_c('img',{staticClass:"img85",attrs:{"src":require("@/assets/curso/square_05.svg"),"alt":"alt"}})])]),_c('div',{staticClass:"col"},[_c('p',{staticClass:"mb-0"},[_vm._v("Entre los errores más comunes que se encuentran, están: no usar mayúsculas, la falta de tildes y la utilización incorrecta y arbitraria de la puntuación en los párrafos; todo esto dado por el desconocimiento de las reglas ortográficas y otros factores que influyen, como: falta de lectura, no usar el diccionario, ausencia de motivación crítica y constructiva para escribir bien.")])])]),_c('p',[_vm._v("Ampliar el vocabulario se consigue a través de la lectura, ya que se logra alcanzar una buena memoria gráfica, dado que el ser humano aprende más de manera visual. Por esto, la invitación es a comenzar a hacer un buen uso del lenguaje. La utilización de normas y reglas gramaticales facilitan que la comunicación sea apropiada, lo cual es primordial en el desarrollo de cualquier comunicación y, mucho más, en la comunicación de carácter científico. Por ello, se orientan las siguientes recomendaciones, las cuales le ayudarán a mejorar la redacción y la ortografía.")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"col-lg-5"},[_c('figure',[_c('img',{staticClass:"m-auto",attrs:{"src":require("@/assets/curso/tema4/t4_12.png"),"alt